<script setup>
import { ref, onMounted, nextTick, defineExpose } from "vue";
import { Chart, registerables } from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";
import axios from "axios";

Chart.register(...registerables, ChartDataLabels);

const props = defineProps({
  settings: {
    type: Object,
    required: true,
  },
});

const colors = {
  green: "#10b981",
  orange: "#ffa600",
  red: "#ef4444",
};

const chart = ref(null);
const chartRef = ref(null);

const fetchData = async () => {
    let response;
    if(props.settings.api.params){
        response = await axios.get(props.settings.api.url, {
            params: {
                params: props.settings.api.params,
            },
        });
    } else{
        response = await axios.get(props.settings.api.url);
    }
    let labels;
    if(props.settings.columns.label){
        labels = response.data.map(
            (row) => row[props.settings.columns.label]
        );
    } else {
        labels = props.settings.labels
    }
    const values = {};

    props.settings.columns.datas.forEach((col) => {
        values[col] = response.data.map((row) => parseInt(row[col], 10) || 0);
    });
    return { labels, values };
};

const generateDatasets = (datas) => {
    if(props.settings.type === 'line'){
        return props.settings.columns.datas.map((column, index) => ({
            label: props.settings.labels[index],
            data: datas[column],
            borderColor: colors[props.settings.colors[index]],
            tension: 0,
            fill: false,
        }));
    } else if(props.settings.type === 'bar'){
        return props.settings.columns.datas.map((column, index) => ({
            label: props.settings.labels[index],
            data: datas[column],
            backgroundColor: colors[props.settings.colors[index]],
        }));
    } else if(props.settings.type === 'pie'){
        let dataset = {
            data: [],
            backgroundColor: []
        };
        props.settings.columns.datas.forEach((column, index) => {
            dataset.data.push(datas[column][0]);
            dataset.backgroundColor.push(props.settings.colors[index]);
        });
        return [dataset];
    }
};
const generateOptions = () => {
    if(["line", "bar"].includes(props.settings.type)){
        return {
            responsive: true,
            maintainAspectRatio: false,
            plugins: {
                datalabels: {
                    display: false,
                },
            },
        }
    } else if(["pie"].includes(props.settings.type)){
        return {
            responsive: true,
            maintainAspectRatio: false,
            plugins: {
                datalabels: {
                color: "#fff",
                font: { weight: "bold", size: 14 },
                formatter: (value) => value,
                },
            },
        }
    }
}

const generatePlugins = () => {
    if(["line", "bar"].includes(props.settings.type)) return null;
    else if(["pie"].includes(props.settings.type)) return [ChartDataLabels];
}

const renderChart = async () => {
  await nextTick();
  if (!chartRef.value) return;

  const { labels, values } = await fetchData();
  console.log("values : ", values);

  if (chart.value) {
    chart.value.destroy();
  }

  chart.value = new Chart(chartRef.value.getContext("2d"), {
    type: props.settings.type,
    data: {
      labels: labels,
      datasets: generateDatasets(values),
    },
    options: generateOptions(),
    plugins: generatePlugins()
  });
};

defineExpose({
  renderChart,
})

onMounted(() => {
  renderChart();
});
</script>

<template>
    <div class="card h-100">
        <div class="card-body">
        <h5 class="card-title text-danger">{{ props.settings.title }}</h5>
        <canvas ref="chartRef" class="w-100" style="height: 300px;"></canvas>
    </div>
  </div>
</template>

<style scoped>
canvas {
  width: 100% !important;
  max-height: 300px;
}
.nav-tabs .nav-link.active {
  background-color: #dc3545;
  color: #fff;
}
.nav-tabs .nav-link {
  color: #000; /* Warna hitam untuk text tab tidak aktif */
}
.card {
  width: 100%;
}
</style>